import React, { useEffect, useState } from 'react';

const API_KEY = '74f4c65fa40120881e93ed2e2e832c7d'; // <- paste your key here
const CITY = 'Leicester'; // or detect via geolocation

function Weather() {
  const [weather, setWeather] = useState(null);

  useEffect(() => {
    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${CITY}&units=metric&appid=${API_KEY}`)
      .then(res => res.json())
      .then(data => {
        setWeather({
          temp: data.main.temp,
          condition: data.weather[0].main,
          icon: data.weather[0].icon
        });
      })
      .catch(console.error);
  }, []);

  if (!weather) return <p className="text-gray-500 italic">Loading weather...</p>;

  return (
    <div className="flex items-center bg-blue-100 text-blue-900 p-4 rounded-xl shadow max-w-sm mt-4">
      <img
        src={`https://openweathermap.org/img/wn/${weather.icon}@2x.png`}
        alt={weather.condition}
        className="w-12 h-12 mr-4"
      />
      <div>
        <p className="font-semibold">{CITY}</p>
        <p>{weather.temp}Â°C â€“ {weather.condition}</p>
      </div>
    </div>
  );
}

export default Weather;
